import React from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import portfolio_List from '../static/database/portfolio_List'
export default class Example extends React.Component {
  static getInitialProps({query}) {
      return {
        portfolio_List: portfolio_List 
      }
  }
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      activeTab: '1'
    };
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }
  render() {
    return (
      <div>
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.toggle('1'); }}
            >
              Portfolio
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.toggle('2'); }}
            >
              About me
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '3' })}
              onClick={() => { this.toggle('3'); }}
            >
              Contract
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={this.state.activeTab}>
         
          <TabPane tabId="1">
            <Row>
                {
                    portfolio_List.map((portfolio)=> (
                      
                        <Col key={portfolio.id} sm="4" className="cardThumbnail">
                            <Card body>
                                <CardText>
                                    <div className="thumbnailImage">
                                        <img src={`static/images/portfolio/${portfolio.image_url}`}/>
                                    </div>
                                </CardText>
                            </Card>
                        </Col>
                    ))
                }
            </Row>
          </TabPane>
          <TabPane tabId="2">
              <div className="cardAbout_me col-md-8">
                  <h3>about me</h3>
              </div>
          </TabPane>
        </TabContent>
        <style>{`
            .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
                color: #fff !important;
                background-color: #21241a;
                border-color: transparent !important;
            }
            .nav-tabs {
                width: 350px;
                margin: auto;
                margin-bottom: 30px;
                border-bottom: 0;
            }
            a {
                text-transform: capitalize;
            }
            .cardThumbnail {
                margin-bottom:30px;
            }
            .thumbnailImage {
                height:345px;
                overflow:hidden;
            }
            .thumbnailImage img {
                width:100%;
                height: auto;
                object-fit: cover;
            }
            .card-body {
                padding:0;
            }
            .cardAbout_me {
                height:300px;
                background-color:#fff;
                margin:auto;
                display:block;
                border-radius:6px;
                box-shadow:0px 2px 10px #bdbcbc;
                padding:16px;
            }
            @media screen and (max-width: 480px) and (max-width: 600px) {
                .nav-tabs {
                    width: 100% !important;
                    margin: auto;
                    margin-bottom: 30px;
                    border-bottom: 0;
                    font-size: 12px;
                }
                .thumbnailImage {
                    height: auto;
                    overflow: hidden;
                }
            }
        `}</style>
      </div>
    );
  }
}


